'use client';

import React, { useMemo } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { shortcutRegistry, ShortcutCategory } from './ShortcutRegistry';
import { cn } from '@/lib/utils';

interface ShortcutHelpDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  currentPage?: string;
}

const CATEGORY_LABELS: Record<ShortcutCategory, string> = {
  [ShortcutCategory.NAVIGATION]: 'Navigation',
  [ShortcutCategory.SEARCH]: 'Search',
  [ShortcutCategory.ACTIONS]: 'Actions',
  [ShortcutCategory.VIEWS]: 'Views',
  [ShortcutCategory.CUSTOM]: 'Custom',
  [ShortcutCategory.TRADING]: 'Trading',
  [ShortcutCategory.HELP]: 'Help',
};

function formatKeyDisplay(key: string): string {
  const parts = key.toLowerCase().split('+');
  return parts.map(part => {
    switch (part) {
      case 'ctrl': return 'Ctrl';
      case 'cmd': return 'âŒ˜';
      case 'option': return 'Option';
      case 'alt': return 'Alt';
      case 'shift': return 'Shift';
      case 'enter': return 'Enter';
      case 'esc': return 'Esc';
      case 'backspace': return 'âŒ«';
      case ' ': return 'Space';
      default: return part.toUpperCase();
    }
  }).join(' + ');
}

function ShortcutItem({ shortcut }: { shortcut: any }) {
  return (
    <div className="flex items-center justify-between py-2 px-3 border-b last:border-0">
      <span className="text-sm text-muted-foreground">{shortcut.description}</span>
      <kbd className="px-2 py-1 bg-muted rounded text-xs font-mono">
        {formatKeyDisplay(shortcut.key)}
      </kbd>
    </div>
  );
}

function ShortcutGroupDisplay({ 
  category, 
  shortcuts 
}: { 
  category: ShortcutCategory; 
  shortcuts: any[] 
}) {
  if (shortcuts.length === 0) return null;
  
  return (
    <div className="space-y-1">
      <h4 className="text-sm font-medium text-muted-foreground uppercase tracking-wider">
        {CATEGORY_LABELS[category]}
      </h4>
      <div className="border rounded-lg divide-y">
        {shortcuts.map((shortcut, idx) => (
          <ShortcutItem key={idx} shortcut={shortcut} />
        ))}
      </div>
    </div>
  );
}

export function ShortcutHelpDialog({ 
  open, 
  onOpenChange, 
  currentPage 
}: ShortcutHelpDialogProps) {
  const globalShortcuts = useMemo(() => {
    const all = shortcutRegistry.getGlobalShortcuts();
    const grouped = new Map<ShortcutCategory, any[]>();
    
    all.forEach(shortcut => {
      if (!grouped.has(shortcut.category)) {
        grouped.set(shortcut.category, []);
      }
      grouped.get(shortcut.category)!.push(shortcut);
    });
    
    return grouped;
  }, []);

  const pageShortcuts = useMemo(() => {
    if (!currentPage) return null;
    
    const all = shortcutRegistry.getPageShortcuts(currentPage);
    const grouped = new Map<ShortcutCategory, any[]>();
    
    all.forEach(shortcut => {
      if (!grouped.has(shortcut.category)) {
        grouped.set(shortcut.category, []);
      }
      grouped.get(shortcut.category)!.push(shortcut);
    });
    
    return grouped;
  }, [currentPage]);

  const categories = Object.values(ShortcutCategory);

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-2xl max-h-[80vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            Keyboard Shortcuts
          </DialogTitle>
        </DialogHeader>

        <Tabs defaultValue="global" className="w-full">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="global">Global</TabsTrigger>
            {currentPage && <TabsTrigger value="page">{currentPage}</TabsTrigger>}
          </TabsList>

          <TabsContent value="global" className="space-y-4 mt-4">
            {categories.map(category => {
              const shortcuts = globalShortcuts.get(category) || [];
              return (
                <ShortcutGroupDisplay
                  key={category}
                  category={category}
                  shortcuts={shortcuts}
                />
              );
            })}
          </TabsContent>

          {currentPage && pageShortcuts && (
            <TabsContent value="page" className="space-y-4 mt-4">
              <div className="text-sm text-muted-foreground mb-4">
                Shortcuts available on the {currentPage} page
              </div>
              {categories.map(category => {
                const shortcuts = pageShortcuts.get(category) || [];
                return (
                  <ShortcutGroupDisplay
                    key={category}
                    category={category}
                    shortcuts={shortcuts}
                  />
                );
              })}
            </TabsContent>
          )}
        </Tabs>

        <div className="mt-6 pt-4 border-t text-xs text-muted-foreground text-center">
          Press <kbd className="px-1 bg-muted rounded">Esc</kbd> to close this dialog
        </div>
      </DialogContent>
    </Dialog>
  );
}
