'use client';

import { useEffect, useCallback, useRef } from 'react';
import { shortcutRegistry, ShortcutKey } from './ShortcutRegistry';

export type { ShortcutCategory } from './ShortcutRegistry';

export function useKeyboardShortcuts(
  shortcuts: Array<{
    key: ShortcutKey;
    description: string;
    action: () => void;
    preventDefault?: boolean;
  }>,
  dependencies: readonly any[] = [],
  enabled: boolean = true
) {
  const enabledRef = useRef(enabled);
  enabledRef.current = enabled;

  const handleKeyDown = useCallback((event: KeyboardEvent) => {
    if (!enabledRef.current) return;

    for (const shortcut of shortcuts) {
      if (shortcutRegistry.matches(event, shortcut.key)) {
        event.preventDefault();
        event.stopPropagation();
        shortcut.action();
        return;
      }
    }
  }, [shortcuts]);

  useEffect(() => {
    if (!enabled) return;

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [handleKeyDown, enabled, ...dependencies]);
}

export function useGlobalShortcuts(
  shortcuts: Array<{
    key: ShortcutKey;
    description: string;
    action: () => void;
    preventDefault?: boolean;
  }>
) {
  useKeyboardShortcuts(shortcuts, [], true);
}

export function usePageShortcuts(
  pageName: string,
  shortcuts: Array<{
    key: ShortcutKey;
    description: string;
    action: () => void;
    preventDefault?: boolean;
  }>
) {
  useKeyboardShortcuts(shortcuts, [pageName], true);
}

export function useSearchShortcut(
  onOpen: () => void,
  enabled: boolean = true
) {
  useKeyboardShortcuts([
    {
      key: '/',
      description: 'Open search',
      action: onOpen,
      preventDefault: true,
    },
    {
      key: 'ctrl+k',
      description: 'Open search (Windows/Linux)',
      action: onOpen,
      preventDefault: true,
    },
    {
      key: 'cmd+k',
      description: 'Open search (Mac)',
      action: onOpen,
      preventDefault: true,
    },
  ], [onOpen], enabled);
}

export function useHelpShortcut(
  onOpen: () => void,
  enabled: boolean = true
) {
  useKeyboardShortcuts([
    {
      key: '?',
      description: 'Show keyboard shortcuts',
      action: onOpen,
      preventDefault: true,
    },
  ], [onOpen], enabled);
}

export function useNavigationShortcuts(
  shortcuts: Record<string, () => void>,
  enabled: boolean = true
) {
  useKeyboardShortcuts([
    {
      key: 'g p',
      description: 'Go to portfolios',
      action: shortcuts.onGoToPortfolios || (() => {}),
    },
    {
      key: 'g s',
      description: 'Go to screener',
      action: shortcuts.onGoToScreener || (() => {}),
    },
    {
      key: 'g m',
      description: 'Go to market overview',
      action: shortcuts.onGoToMarket || (() => {}),
    },
    {
      key: 'g n',
      description: 'Go to news',
      action: shortcuts.onGoToNews || (() => {}),
    },
    {
      key: 'g w',
      description: 'Go to watchlist',
      action: shortcuts.onGoToWatchlist || (() => {}),
    },
  ], Object.values(shortcuts), enabled);
}
