import { shortcutRegistry, ShortcutCategory } from './ShortcutRegistry';
import { useSearchShortcut, useHelpShortcut, useNavigationShortcuts } from './useKeyboardShortcuts';
import { ShortcutHelpDialog } from './ShortcutHelpDialog';

export { shortcutRegistry, ShortcutCategory };
export { useSearchShortcut, useHelpShortcut, useNavigationShortcuts };
export { ShortcutHelpDialog };

export function registerDefaultShortcuts() {
  shortcutRegistry.clear();

  shortcutRegistry.register({
    key: '?',
    description: 'Show keyboard shortcuts help',
    action: () => {},
    category: ShortcutCategory.HELP,
    enabled: true,
  });

  shortcutRegistry.register({
    key: '/',
    description: 'Open global search',
    action: () => {},
    category: ShortcutCategory.SEARCH,
    enabled: true,
  });

  shortcutRegistry.register({
    key: 'ctrl+k',
    description: 'Open global search (Windows/Linux)',
    action: () => {},
    category: ShortcutCategory.SEARCH,
    enabled: true,
  });

  shortcutRegistry.register({
    key: 'cmd+k',
    description: 'Open global search (Mac)',
    action: () => {},
    category: ShortcutCategory.SEARCH,
    enabled: true,
  });

  shortcutRegistry.register({
    key: 'esc',
    description: 'Close modal/dropdown',
    action: () => {},
    category: ShortcutCategory.ACTIONS,
    enabled: true,
  });

  shortcutRegistry.register({
    key: 'g p',
    description: 'Go to portfolios',
    action: () => {},
    category: ShortcutCategory.NAVIGATION,
    enabled: true,
  });

  shortcutRegistry.register({
    key: 'g s',
    description: 'Go to screener',
    action: () => {},
    category: ShortcutCategory.NAVIGATION,
    enabled: true,
  });

  shortcutRegistry.register({
    key: 'g m',
    description: 'Go to market overview',
    action: () => {},
    category: ShortcutCategory.NAVIGATION,
    enabled: true,
  });

  shortcutRegistry.register({
    key: 'g n',
    description: 'Go to news',
    action: () => {},
    category: ShortcutCategory.NAVIGATION,
    enabled: true,
  });

  shortcutRegistry.register({
    key: 'g w',
    description: 'Go to watchlist',
    action: () => {},
    category: ShortcutCategory.NAVIGATION,
    enabled: true,
  });

  shortcutRegistry.register({
    key: 'ctrl+/',
    description: 'Toggle theme (dark/light)',
    action: () => {},
    category: ShortcutCategory.VIEWS,
    enabled: true,
  });

  shortcutRegistry.register({
    key: 'r',
    description: 'Refresh data',
    action: () => {},
    category: ShortcutCategory.ACTIONS,
    page: 'portfolio',
    enabled: true,
  });

  shortcutRegistry.register({
    key: 's',
    description: 'Run screen',
    action: () => {},
    category: ShortcutCategory.ACTIONS,
    page: 'screener',
    enabled: true,
  });

  shortcutRegistry.register({
    key: 'a',
    description: 'Add to watchlist',
    action: () => {},
    category: ShortcutCategory.ACTIONS,
    page: 'asset',
    enabled: true,
  });
}
