export enum ShortcutCategory {
  NAVIGATION = 'navigation',
  SEARCH = 'search',
  ACTIONS = 'actions',
  VIEWS = 'views',
  CUSTOM = 'custom',
  TRADING = 'trading',
  HELP = 'help',
}

export interface Shortcut {
  key: string;
  description: string;
  action: () => void;
  category: ShortcutCategory;
  page?: string;
  enabled: boolean;
  customizable?: boolean;
}

export type ShortcutKey = string;

export interface ShortcutConflict {
  key: ShortcutKey;
  shortcuts: Shortcut[];
}

export class ShortcutRegistry {
  private shortcuts: Map<string, Shortcut> = new Map();
  private pageShortcuts: Map<string, Shortcut[]> = new Map();

  register(shortcut: Shortcut): void {
    const key = this.normalizeKey(shortcut.key);
    const pageKey = shortcut.page || 'global';
    
    this.shortcuts.set(key, shortcut);
    
    const pageList = this.pageShortcuts.get(pageKey) || [];
    pageList.push(shortcut);
    this.pageShortcuts.set(pageKey, pageList);
  }

  unregister(key: string): void {
    const normalizedKey = this.normalizeKey(key);
    const shortcut = this.shortcuts.get(normalizedKey);
    
    if (shortcut) {
      this.shortcuts.delete(normalizedKey);
      
      const pageKey = shortcut.page || 'global';
      const pageList = this.pageShortcuts.get(pageKey) || [];
      const filtered = pageList.filter(s => s.key !== key);
      this.pageShortcuts.set(pageKey, filtered);
    }
  }

  getShortcut(key: string): Shortcut | undefined {
    return this.shortcuts.get(this.normalizeKey(key));
  }

  getAllShortcuts(): Shortcut[] {
    return Array.from(this.shortcuts.values());
  }

  getPageShortcuts(page: string): Shortcut[] {
    return this.pageShortcuts.get(page) || [];
  }

  getGlobalShortcuts(): Shortcut[] {
    return this.pageShortcuts.get('global') || [];
  }

  getShortcutsByCategory(category: ShortcutCategory): Shortcut[] {
    return this.getAllShortcuts().filter(s => s.category === category);
  }

  checkConflict(key: string, page?: string): Shortcut | undefined {
    const normalizedKey = this.normalizeKey(key);
    
    const conflicts = this.getAllShortcuts().filter(s => {
      const sKey = this.normalizeKey(s.key);
      return sKey === normalizedKey && (s.page === page || (!s.page && !page));
    });

    return conflicts.length > 0 ? conflicts[0] : undefined;
  }

  detectConflicts(shortcuts: Shortcut[]): ShortcutConflict[] {
    const keyGroups: Map<string, Shortcut[]> = new Map();
    
    shortcuts.forEach(shortcut => {
      const normalizedKey = this.normalizeKey(shortcut.key);
      const group = keyGroups.get(normalizedKey) || [];
      group.push(shortcut);
      keyGroups.set(normalizedKey, group);
    });

    const conflicts: ShortcutConflict[] = [];
    keyGroups.forEach((shortcuts, key) => {
      if (shortcuts.length > 1) {
        conflicts.push({ key, shortcuts });
      }
    });

    return conflicts;
  }

  normalizeKey(key: string): string {
    return key.toLowerCase().trim();
  }

  parseKey(key: string): { ctrl: boolean; shift: boolean; alt: boolean; key: string } {
    const parts = key.toLowerCase().split('+');
    let actualKey = parts[parts.length - 1];
    
    return {
      ctrl: parts.includes('ctrl') || parts.includes('control'),
      shift: parts.includes('shift'),
      alt: parts.includes('alt') || parts.includes('option'),
      key: actualKey,
    };
  }

  matches(event: KeyboardEvent, shortcutKey: string): boolean {
    const parsed = this.parseKey(shortcutKey);
    
    const keyMatch = event.key.toLowerCase() === parsed.key ||
                     event.code === `Key${parsed.key.toUpperCase()}`;
    
    const ctrlMatch = parsed.ctrl ? event.ctrlKey || event.metaKey : !event.ctrlKey && !event.metaKey;
    const shiftMatch = parsed.shift ? event.shiftKey : !event.shiftKey;
    const altMatch = parsed.alt ? event.altKey : !event.altKey;
    
    return keyMatch && ctrlMatch && shiftMatch && altMatch;
  }

  clear(): void {
    this.shortcuts.clear();
    this.pageShortcuts.clear();
  }
}

export const shortcutRegistry = new ShortcutRegistry();
